import React from 'react'
import Image from 'next/image'
import Computer from '@/public/img/Computer.png'
import Style from '@/app/Components/css/about.module.css'
import { Link, Button } from '@nextui-org/react'
export default function aboutus() {
  return (
    <section id='about' className={Style.all}>
      <div className={Style.about}>
        <div className={Style.container}>
          <Image
            src={Computer}
            alt='computer'
            className={Style.img}
          />
        </div>
        <div >
          <h2 className={Style.title}>ABOUT US</h2>
          <h2 className={Style.subtitle}><span className={Style.spancolor}>Discover The Potential Of</span>
            <span className={Style.spantext}> Innovation</span>.</h2>
          <p className={Style.description}>
            Welcome To Cronos Studio Indonesia,Where Innovation Is More Than A Buzzword-It's
            Our Driving Force. At The Intersection Of Creativity And Technology We Are A Dynamic
            Software House Committed To Redefining Possibilities.
          </p>
          <p className={Style.description}>
            Cronos Studio Indonesia Is More Than Just A Team Of Developers; We Are A Collective
            Of Visionaries, Problem Solvers, And Innovators. Our Journey Began With A Simple Yet
            Powerful Idea: To Harness The Potential Of Technology To Transform The Way
            Businesses Operate And Thrive In The Digital Landscape.
          </p>
          {/* <a href='/about' className={Style.btn}>READ MORE</a> */}

          <Button className={Style.button} href="/about" as={Link} color='primary' variant="bordered" size='md' radius='sm'>READ MORE</Button>
        </div>
      </div>
    </section>
  );
}